"use client";
import { useActionState, useState } from "react";
import { useRouter } from "next/navigation";
import { createUserAction } from "@/lib/actions/CreateUser";

type AuthMode = "login" | "signup";

const AuthPage = () => {
  const [mode, setMode] = useState<AuthMode>("login");
  const [enteredData, setEnteredData] = useState({
    name: "",
    email: "",
    password: "",
  });

  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const router = useRouter();

  const [formState, formAction] = useActionState();

  return (
    <div className="relative flex h-screen flex-col bg-black md:mt-[120px] md:items-center md:bg-transparent">
      <form
        action={actionForm}
        className="relative mt-24 space-y-6 rounded bg-black/75 py-8 px-6 md:mt-0 md:max-w-md md:px-14"
      >
        <h1 className="text-3xl font-semibold text-white text-center">
          {mode === "login" ? "Sign In" : "Create Account"}
        </h1>

        {error && (
          <div className="p-3 bg-red-600 text-white rounded text-sm">
            {error}
          </div>
        )}

        <div className="space-y-4">
          {mode === "signup" && (
            <label className="inline-block w-full">
              <input
                type="text"
                placeholder="Name"
                value={enteredData.name}
                className="input"
                disabled={isLoading}
              />
            </label>
          )}

          <label className="inline-block w-full">
            <input
              type="email"
              placeholder="Email"
              value={enteredData.email}
              className="input"
              required
              disabled={isLoading}
            />
          </label>

          <label className="inline-block w-full">
            <input
              type="password"
              placeholder="Password"
              value={enteredData.password}
              className="input"
              required
              minLength={mode === "signup" ? 6 : 4}
              disabled={isLoading}
            />
          </label>
        </div>

        <button
          type="submit"
          disabled={isLoading}
          className={`w-full rounded bg-[#e50914] py-3 font-semibold ${
            isLoading ? "opacity-60 cursor-not-allowed" : ""
          }`}
        >
          {isLoading
            ? mode === "login"
              ? "Signing In..."
              : "Signing Up..."
            : mode === "login"
            ? "Sign In"
            : "Sign Up"}
        </button>

        <div className="flex justify-between items-center text-[#737373] text-sm">
          <div className="flex items-center">
            <input
              type="checkbox"
              id="remember"
              className="mr-1"
              disabled={isLoading}
            />
            <label htmlFor="remember">Remember me</label>
          </div>
          <a href="#" className="hover:underline">
            Need help?
          </a>
        </div>

        <div className="text-[#737373]">
          {mode === "login" ? (
            <>
              New to Netflix?{" "}
              <button
                type="button"
                onClick={toggleMode}
                className="text-white hover:underline"
                disabled={isLoading}
              >
                Sign up now
              </button>
            </>
          ) : (
            <>
              Already have an account?{" "}
              <button
                type="button"
                onClick={toggleMode}
                className="text-white hover:underline"
                disabled={isLoading}
              >
                Sign in now
              </button>
            </>
          )}
        </div>

        <div className="text-[#737373] text-xs">
          <p>
            This page is protected by Google reCAPTCHA to ensure you&#39;re not
            a bot.{" "}
            <a href="#" className="text-blue-500 hover:underline">
              Learn more.
            </a>
          </p>
        </div>
      </form>
    </div>
  );
};

export default AuthPage;
